import classes from './App.module.css';
import React from 'react';

import Productpreview from './Productpreview/Productpreview';
import Productdetails from './Productdetails/Productdetails';
import Topbar from './Topbar/Topbar';
import ProductData from './ProductData';



class App extends React.Component {
  state={
    productdata: ProductData,
    currentproductpos: 0,
    show: false,

  }

  oncolor(pos){
    this.setState({currentproductpos: pos});
  }

  onfeature(pos){
    let update = false;
    if(pos===0)
    {
      update=true;
    }
    this.setState({show: update})
  }
  render(){
    return (
      <div className="App">
        <Topbar/>

        <div className={classes.maincontainer}>
          
          <div className={classes.Productpreview}>
            <Productpreview show={this.state.show} current={this.state.productdata.colorOptions[this.state.currentproductpos].imageUrl} />
          </div>

          <div className={classes.Productdetails}>
            <Productdetails data={this.state.productdata} oncolorkey={this.oncolor.bind(this)} currentpos={this.state.currentproductpos}
            onfeaturekey={this.onfeature.bind(this)} show={this.state.show}/>
          </div>

        </div>
      </div>
    );
  }
}

export default App;
