import React from 'react'
import classes from './Productdetails.module.css'


function Productdetails(props) {
    const colorOptions =props.data.colorOptions.map((item,pos)=>{
        const classarr=[classes.productimage]

        if(pos===props.currentpos){
            classarr.push(classes.selectedimg);
        }
        return (
            <img key={pos} onClick={()=> props.oncolorkey(pos)} className={classarr.join(' ')} src={item.imageUrl} alt={item.styleName}/>
        );
    })
    const feature= props.data.featureList.map((item,pos)=>{
        const classarr=[classes.featurebtn]

        if(pos===0 && props.show){
            classarr.push(classes.selectedfeature);
        }
        else if(pos===1 && !props.show){
            classarr.push(classes.selectedfeature);
        }
        return(
            <button key={pos} onClick={()=> props.onfeaturekey(pos)} className={classarr.join(' ')}>{item}</button>
        )
    })
    return (
        <div>
                <h1 className={classes.producttitle}>{props.data.title}</h1>
                <p className={classes.productdescription}>{props.data.description}</p>

                <h3 className={classes.sectionheading}>select color</h3>
                <div>
                {colorOptions}            
                </div>
                <h3 className={classes.sectionheading}>feature</h3>
                <div>
                {feature}
                </div>
                <div><button className={classes.featurebtn1}>Buy now</button></div>
        </div>
  );
}

export default Productdetails